.masonry-grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-left: -10px;
  width: auto;
  margin-block: 3rem;
}

.masonry-grid_column {
  padding-left: 10px;
  background-clip: padding-box;
}

.masonry-grid_column > div {
  margin-bottom: 20px;
  margin-inline: 5px;
}
